.search-content {
  max-width: 100%;
  min-height: 100vh;
  padding: 0px 30px;
  padding-top: 70px;
}

.symbols-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 5px 0px 10px 0px;
}

.symbol-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 32%;
  min-height: 235px;
  padding: 20px;
  margin: auto auto 30px auto;
  text-align: center;
  border: 1px solid #ddd;
  -webkit-transition: border-color 0.5s ease;
  -o-transition: border-color 0.5s ease;
  transition: border-color 0.5s ease;
  color: black !important;
}

.symbol-box h2 {
  font-size: 60px;
  margin: 0
}

.symbol-box .meaning {
  font-style: italic;
  font-size: 14px;
  word-spacing: 1.5px;
  line-height: 18px;
}

.symbol-box:hover {
  border-color: rebeccapurple;
}

@media screen and (max-width: 785px) {
  .symbol-box {
    width: 48%;
    min-height: 250px;
  }
}

@media screen and (max-width: 650px) {
  .symbol-box {
    width: 100%;
    min-height: 250px;
  }
}



